import { Injectable } from '@angular/core';
import { Router } from '@angular/router';
import {Subject} from 'rxjs';
import * as firebase from 'firebase';

@Injectable({
  providedIn: 'root'
})
export class ItemService {
  private eventSubject = new Subject<any>();
  usertype ="visitor";
  db = firebase.firestore();
  
  public items:Array<any>=[];
  
  public orders:Array<any>=[];
  
  public cart:Array<any>=[];
  
  publishEvent(data:any) {
  this.eventSubject.next(data);
  }
  getObservable(): Subject<any> {
  return this.eventSubject;
  }
  
  constructor(public router:Router) {
  
  var self = this;
    this.db.collection("CartOrder").onSnapshot(function(querySnapshot) {
      self.orders =[];
      querySnapshot.forEach(function(doc) {
        var test = doc.data();
        self.orders.push({oid:test.oid,uid:test.uid,cart:test.cart,
          total:test.total,date:test.date,quantity:test.quantity,id:doc.id});
      });
      self.publishEvent({foo: 'bar'});
    });
  
    this.db.collection("items")
    .onSnapshot(function(querySnapshot) {
      console.log("items list changed...");
      self.items = [];
      querySnapshot.forEach(function(doc) {
        var item = doc.data();
        // console.log(doc.id)
        self.items.push({title:item.title,price:item.price,
          image:item.image,description:
          item.description,category:item.category,uid:item.uid,id:doc.id});
        });
        self.publishEvent({ foo: 'bar'});
        console.log("Items reloaded");
      });
  
    this.db.collection("cart")
    .onSnapshot(function(querySnapshot) {
      console.log("cart changed...");
      self.cart = [];
      querySnapshot.forEach(function(doc) {
        var cart = doc.data();
        // console.log(doc.id)
        self.cart.push({cost:cart.cost,quantity:cart.quantity,
          title:cart.title,image:cart.image,uid:cart.uid,id:doc.id});
        });
        self.publishEvent({ foo: 'bar'});
        console.log("Cart reloaded");
      });
    }
  
    setUsertype(type){
  
      var self=this;
  
      this.db.collection("CartOrder").where("uid","==",firebase.auth().currentUser.uid)
      .onSnapshot(function(querySnapshot) {
        self.orders =[];
        querySnapshot.forEach(function(doc) {
          var test = doc.data();
          self.orders.push({oid:test.oid,uid:test.uid,cart:test.cart,
            total:test.total,date:test.date,quantity:test.quantity,id:doc.id});
        });
        self.publishEvent({foo: 'bar'});
      });
  
        this.usertype= type;
        console.log("usertype set as: "+type);
        if (this.usertype == "visitor"){
          this.db.collection("items")
          .onSnapshot(function(querySnapshot) {
            console.log("items list changed...........");
            self.items = [];
            querySnapshot.forEach(function(doc) {
              var item = doc.data();
              self.items.push({title:item.title,price:item.price,
                image:item.image,description:
                item.description,category:item.category,uid:item.uid,id:doc.id});
              });
              self.publishEvent({ foo: 'bar'});
              console.log("Items reloaded");
            });
          } else {
            this.db.collection("items").where("uid","==",firebase.auth().currentUser.uid)
            .onSnapshot(function(querySnapshot) {
              console.log("items list changed...........");
              self.items = [];
              querySnapshot.forEach(function(doc) {
                var item = doc.data();
                self.items.push({title:item.title,price:item.price,
                  image:item.image,description:
                  item.description,category:item.category,uid:item.uid,id:doc.id});
                });
                self.publishEvent({ foo: 'bar'});
                console.log("Items reloaded");
              });
            }
  
            this.db.collection("cart").where("uid","==",firebase.auth().currentUser.uid)
            .onSnapshot(function(querySnapshot) {
            console.log("cart changed...");
            self.cart = [];
            querySnapshot.forEach(function(doc) {
            var cart = doc.data();
              self.cart.push({cost:cart.cost,quantity:cart.quantity,
              title:cart.title,image:cart.image,uid:cart.uid,id:doc.id});
              });
            self.publishEvent({ foo: 'bar'});
            console.log("Cart reloaded");
          });
        }
  
          getUsertype() {
            return this.usertype;
          }
  
          createItem(title,price,category,image,description) {
            var self=this;
            var uid=null;
            if (firebase.auth().currentUser !=  null){
              uid=firebase.auth().currentUser.uid
              console.log(uid, " :****** uid");
            }
            else{
              console.log(" no user logged in, no item created")
            }
            var db = firebase.firestore();
            db.collection("items").add({
              'uid':uid,
              'title': title,
              'price':price,
              'category':category,
              'description': description,
              'image':image
            })
            .then(function(docRef) {
              console.log("Document written with ID: ", docRef.id);
  
              //update this products arrays
            })
            .catch(function(error) {
              console.error("Error adding document: ", error);
            });
          }
  
          deleteOrder(id) {
            let self = this;
            let db = firebase.firestore();
            db.collection("CartOrder").doc(id).delete().then(function() {
              self.router.navigate(["/tabs/tab2"]);
            }).catch(function(error) {
              console.log("error with removing doc");
            });
          }
  
          deleteItem(id){
            let self = this;
            let db = firebase.firestore();
            db.collection("items").doc(id).delete().then(function() {
              self.router.navigate(["/tabs/tab1"]);
            }).catch(function(error) {
              console.error("error with removing doc");
            })
          }
  
          addToCart(cost,quantity,title,image,uid) {
            var db = firebase.firestore();
            db.collection("cart").add({
              'cost': cost,
              'quantity':quantity,
              'title':title,
              'image':image,
              'uid':uid
              }).then(function (docRef) {
              console.log("Document written with ID: ", docRef.id);
            }).catch(function(error) {
              console.error("Error adding document: ", error);
            });
          }
  
          deleteCart(id){
            let self = this;
            let db = firebase.firestore();
            db.collection("cart").doc(id).delete().then(function() {
              self.router.navigate(["/tabs/tab3"]);
            }).catch(function(error) {
              console.error("error with removing doc");
            })
  
          }
  
          placeOrder(cart,oid,uid,total,date,quantity) {
            var db = firebase.firestore();
            db.collection("CartOrder").add ({
              'cart':cart,
              'oid':oid,
              'uid':uid,
              'total':total,
              'date':date,
              'quantity':quantity
            }).then(function (docRef) {
              console.log("Document written with ID: ", docRef.id);
            }).catch(function(error) {
              console.error("Error adding document: ", error);
            });
          }
  
          updateProduct(value,id) {
            let self = this;
            let db = firebase.firestore();
            console.log(value);
             db.collection("items").doc(id).update(value);
  
          }
  
          }
  
        export const snapshotToArray = snapshot => {
          let returnArr = [];
  
          snapshot.forEach(childSnapshot => {
            let item = childSnapshot.val();
            item.id = childSnapshot.key;
            // console.log(item);
            returnArr.push(item);
          });
  
          return returnArr;
        }
  
